import React from 'react';
import './Tax.css';
import {SmallButton2Page} from "./SmallButton2Page";
import {LowerButton} from "./LowerButton";
import close from '../images/jam_close.png';
import {observer} from "../index";
import {AllPayments} from "./AllPayments";

export let Tax = (props) => {
    return (
        <div className="Tax">
            <div className="title">
                <div className="titleText"> </div>
                <img onClick={() => {props.store.go21(); observer()}} src={close} width={'40px'} height={'40px'} alt="close" className="close"/>
            </div>
            <div className="description">      .<br/>
                      13%     .
            </div>
            <div className="inputZP">
                <div className="inputZPtext">   </div>
                <input placeholder={' '} type='text' className="inputZPinput"></input>
                <div onClick={() => {props.store.addPayments(); observer()}} className="calculateButton"></div>
            </div>
            <AllPayments checkFunc={props.store.checkOneBox} info={props.store.Payments}></AllPayments>
            <div className="twoButtons">
                <div className="twoButtonsText"> ?</div>
                <SmallButton2Page styleProps={`32px;16px;${props.store.twoButtons.first.background}
                ;${props.store.twoButtons.first.color};first`}
                click={props.store.click2Buttons} text={''}
                hover2Buttons={props.store.hover2Buttons}
                leave2Buttons={props.store.leave2Buttons}></SmallButton2Page>

                <SmallButton2Page styleProps={`;;${props.store.twoButtons.second.background}
                ;${props.store.twoButtons.second.color};second`}
                click={props.store.click2Buttons} text={''}
                leave2Buttons={props.store.leave2Buttons}
                hover2Buttons={props.store.hover2Buttons}></SmallButton2Page>
            </div>
            <LowerButton finish={() => {props.store.finish(); props.store.go21(); observer()}}></LowerButton>
        </div>
    );
}